.theading{
    color:rgb(165, 165, 165);
    font-weight: bolder;
}

.pheading{
    color:rgb(165, 165, 165);
    font-weight: bolder;
}
ul{
    list-style-type:none;
}
.exitbutton
{
    padding: 3px 10px;
    background-color: orange;
    color:rgb(70, 70, 70);
    font-size: 20px;
    border-radius: 50%;
    font-weight: bold;
    text-decoration: none;
}
.exitbutton:hover
{
    text-decoration: none;
    color: #000;
}

@media screen and (max-width: 270px) {
    h2,h3,h4 {
      font-size: 8px;
 
    }
    p,ul{
        font-size: 5px;
    
    }
    .imitext{
        font-size: 12px;
        overflow: hidden;
    }
    .exitbutton
    {
        font-size: 5px;
        float: left;
        padding: 2px 4px;
    }
    
}
.appbar
{
    position: sticky;
    top: 0%;
    z-index: 99;
    width: 100%;
}
.btn-primary.focus, .btn-primary:focus {
    -webkit-box-shadow: none!important;
    box-shadow: none!important;
}
